import React, {useEffect, useRef, useReducer} from 'react';

import './PortfolioSites.scss';

function PortfolioSites() {
    const [state, setState] = useReducer(
      ( s, a ) => ({ ...s, ...a }),
      {
          isLoading: true,
          furtherInfo: '',
          hideFurtherInfo: true,
          posts: [],
          setPosts: [],
      }
    )

    const {isLoading, furtherInfo, hideFurtherInfo, posts, setPosts} = state

    const getData = () => {
        fetch("https://jackalexander.uk/wp-json/wp/v2/project?per_page=99")
            .then(response => response.json())
            .then(json => setState({
                    posts : json.map(mapPost),
                    isLoading : false
                })
            )
    }

    const mapPost = (post) => {
        return {
            title: post.title.rendered,
            link: post.link,
            image: post.acf.key_page_1.image.sizes['project-box'],
            year: post.acf.year_published,
            acf: post.acf,
        }
    }

    const getFurtherInfo = (result) => {
        setState({
            furtherInfo : result,
            hideFurtherInfo : false
        })
    }

    const useOutsideAlerter = ( ref, closeFunc ) => {
        const onClickOutside = ( event ) => {
            if ( ref.current && ! ref.current.contains( event.target ) ) {
                closeFunc()
            }
        }

        useEffect( () => {
            document.addEventListener( 'mousedown', onClickOutside )

            return () => document.removeEventListener( 'mousedown', onClickOutside )
        }, [] )
    }

    const wrapperRef = useRef(null)
    useOutsideAlerter(wrapperRef, () => setState({hideFurtherInfo : true}))

    useEffect(() => getData(), [])

    return (
        <div className={'container'}>
            <div className="post-container">
                {isLoading &&
                    <div className="loader"></div>
                }
                {! isLoading && posts.map((result, i) =>
                    <div className={'project-box-wrapper'} onClick={() => getFurtherInfo(result)}>
                        <div className="project-box" key={i} style={{backgroundImage: 'url('+result.image+')' }}>
                            <span className="year">{result.year}</span>
                            <h3>{result.title}</h3>
                        </div>
                    </div>
                )}
            </div>
            {! hideFurtherInfo && furtherInfo &&
                <div className={'further-info'} ref={wrapperRef}>
                    <div className={'close'} onClick={(e) => setState({hideFurtherInfo : true})}>x</div>
                    <div className={'text-holder'}>
                        <h2>{furtherInfo.title}</h2>
                        <div dangerouslySetInnerHTML={{__html: furtherInfo.acf.project_description}} />
                        <p>This {furtherInfo.acf.project_types[0].name} was built in {furtherInfo.year}, using the following tools:</p>
                        {furtherInfo.acf.project_tools &&
                            <p>
                                Projects Tools: {furtherInfo.acf.project_tools.map((result, i) =>
                                    <span>{result.name}</span>
                                )}
                            </p>
                        }
                        {furtherInfo.acf.plugins &&
                            <p>
                                Plugins: {furtherInfo.acf.plugins.map((result, i) =>
                                    <span>{result.name}</span>
                                )}
                            </p>
                        }
                        {furtherInfo.acf.project_foundations &&
                            <p>
                                Foundations: {furtherInfo.acf.project_foundations.map((result, i) =>
                                    <span>{result.name}</span>
                                )}
                            </p>
                        }
                        {furtherInfo.acf.companies &&
                            <p>
                                Made with: {furtherInfo.acf.companies.map((result, i) =>
                                    <span>{result.name}</span>
                                )}
                            </p>
                        }
                        {furtherInfo.acf.project_link &&
                            <a className={'btn'} href={furtherInfo.acf.project_link} target={'_blank'} rel={'noopener noreferrer'}>View website</a>
                        }
                        <div className={'key-page-wrapper'}>
                            {furtherInfo.acf.key_page_1.image &&
                                <a href={furtherInfo.acf.key_page_1.image.url} target={'_blank'} rel={'noopener noreferrer'} className={'key-page'} style={{backgroundImage: 'url('+furtherInfo.acf.key_page_1.image.sizes['project-box']+')' }}>
                                    <p>{furtherInfo.acf.key_page_1.title}</p>
                                </a>
                            }
                            {furtherInfo.acf.key_page_2.image &&
                                <a href={furtherInfo.acf.key_page_2.image.url} target={'_blank'} rel={'noopener noreferrer'} className={'key-page'} style={{backgroundImage: 'url('+furtherInfo.acf.key_page_2.image.sizes['project-box']+')' }}>
                                    <p>{furtherInfo.acf.key_page_2.title}</p>
                                </a>
                            }
                            {furtherInfo.acf.key_page_3.image &&
                                <a href={furtherInfo.acf.key_page_3.image.url} target={'_blank'} rel={'noopener noreferrer'} className={'key-page'} style={{backgroundImage: 'url('+furtherInfo.acf.key_page_3.image.sizes['project-box']+')' }}>
                                    <p>{furtherInfo.acf.key_page_3.title}</p>
                                </a>
                            }
                        </div>
                    </div>
                </div>
            }
            <div className="overlay"></div>
        </div>
    )
}

export default PortfolioSites;