import React, {useState} from 'react';
import { BrowserRouter as Router, Route} from 'react-router-dom';
import Header from '../components/Modules/Header/Header';
import Footer from '../components/Modules/Footer/Footer';
import HomePage from '../pages/Homepage';
import Portfolio from "../pages/Portfolio";
import About from "../pages/About";

function Default() {
  const [siteColor, setSiteColor] = useState(localStorage.getItem('site_color'))

  const updateColor = (siteColor) => {
    const color = null === siteColor ? 'light' : (siteColor === 'dark' ? 'light' : 'dark');

    setSiteColor(color);
    localStorage.setItem('site_color', color)
  }

  const classes = [
    'main',
    siteColor ? siteColor : ''
  ]

  return (
    <React.Fragment>
      <div className={classes.filter(Boolean).join(' ')}>
        <Header siteColor={siteColor} updateColor={updateColor} />
        <Router>
          <Route name="home" exact path="/" component={HomePage} />
          <Route name="about" exact path="/about-me" component={About} />
          <Route name="portfolio" exact path="/portfolio" component={Portfolio} />
        </Router>
        <Footer />
      </div>
    </React.Fragment>
  );
}

export default Default;
